import { connect } from 'react-redux';
import {nowVideoTime,allVideoTime, cardItems,dragCursor,scenePosition,sceneScale,videoIndex,barWidthList,dragDirection,isMouseDown,currentX,leftOffsetWidth,rightOffsetWidth,currentBarIndex,isPlayVideo} from '@/selector/video';
import * as videoAction from '@/action/videoAction';
import TimeLine from './TimeLine.js';

const mapStateToProps = (state) => ({
    //video time-line
    barWidthList:barWidthList(state),
    dragDirection:dragDirection(state),
    isMouseDown:isMouseDown(state),
    currentX:currentX(state),
    leftOffsetWidth:leftOffsetWidth(state),
    rightOffsetWidth:rightOffsetWidth(state),
    currentBarIndex:currentBarIndex(state),
    dragCursor:dragCursor(state),
    //video card
    cardItems: cardItems(state),
    scenePosition: scenePosition(state),
    sceneScale: sceneScale(state),
    videoIndex:videoIndex(state),
    isPlayVideo:isPlayVideo(state),
    nowVideoTime:nowVideoTime(state),
    allVideoTime:allVideoTime(state),
})

const mapDispatchToProps = dispatch => {
    return {
        initTimeLineBarWidth:(length)=>dispatch(videoAction.initTimeLineBarWidth(length)),
        addBarToTimeLineBar:(bar,index)=>dispatch(videoAction.addBarToTimeLineBar(bar,index)),
        deleteBarFromTimeLineBar:(index)=>dispatch(videoAction.deleteBarFromTimeLineBar(index)),
        changeTimeLineBar:(origin,target)=>dispatch(videoAction.changeTimeLineBar(origin,target)),
        setLeftBarWidth:(index,width)=>dispatch(videoAction.setLeftBarWidth(index,width)),
        setRightBarWidth:(index,width)=>dispatch(videoAction.setRightBarWidth(index,width)),
        setDragDirection:(direction)=>dispatch(videoAction.setDragDirection(direction)),
        setMouseDownState:(mouseDown)=>dispatch(videoAction.setMouseDownState(mouseDown)),
        setDragState:(index,drag)=>dispatch(videoAction.setDragState(index,drag)),
        setPosition: (position) => dispatch(videoAction.setPosition(position)),
        setSceneScale: (scale) => dispatch(videoAction.setSceneScale(scale)),
        setCurrentX:(clientx)=>dispatch(videoAction.setCurrentX(clientx)),
        setLeftOffsetWidth:(offset)=>dispatch(videoAction.setLeftOffsetWidth(offset)),
        setRightOffsetWidth:(offset)=>dispatch(videoAction.setRightOffsetWidth(offset)),
        setCurrentBarIndex:(index)=>dispatch(videoAction.setCurrentBarIndex(index)),
        setDragCursor:(cursor)=>dispatch(videoAction.setDragCursor(cursor)),
        playVideoSlice:(index)=>dispatch(videoAction.playVideoSlice(index)),
        changeNowVideoTime:(time)=>dispatch(videoAction.changeNowVideoTime(time)),
        changeAllVideoTime:(time)=>dispatch(videoAction.changeAllVideoTime(time)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TimeLine);
